import axios from "axios";
import {
  RESTAURANT_LIST_LOADING,
  RESTAURANT_LIST_FAILURE,
  RESTAURANT_LIST_SUCCESS,
  SEARCH,
} from "./../constants/actionTypes";

export const FetchRestaurantInfoByState = (stateCode, limit = 20) => async (
  dispatch
) => {
  dispatch({
    type: RESTAURANT_LIST_LOADING,
  });

  try {
    const res = await axios({
      method: "GET",
      url: `https://us-restaurant-menus.p.rapidapi.com/restaurants/state/${stateCode}?limit=${limit}`,
      headers: {
        "X-RapidAPI-Key": "a80182b81emshf292746298162f5p188af1jsn61f07028f462",
        useQueryString: true,
      },
    });

    // console.log(res.data.result.data);

    res.data.result.data.forEach((element) => {
      element.rating = Math.floor(Math.random() * 6);
    });

    dispatch({
      type: RESTAURANT_LIST_SUCCESS,
      payload: {
        restaurants: res.data.result.data,
        selectedState: stateCode,
      },
    });
  } catch (e) {
    dispatch({
      type: RESTAURANT_LIST_FAILURE,
      payload: {
        error: e,
      },
    });
  }
};

export const SyncChange = (cuisine, streetAddress, rating) => {
  return {
    type: SEARCH,
    payload: {
      cuisine,
      streetAddress,
      rating,
    },
  };
};
